/* eslint-disable no-param-reassign */
export default class AppView {
  constructor(clipData) {
    this.clipData = clipData;
    this.currentPage = null;
    this.sliderContainer = null;
    this.pagination = null;
    this.clips = null;
    this.searchInput = null;
  }

  animateSearchBorder() {
    const animateBorder = (e) => { e.target.parentNode.classList.toggle('animate-border'); };
    this.searchInput.addEventListener('focus', animateBorder);
    this.searchInput.addEventListener('blur', animateBorder);
  }

  static renderSearch() {
    return `
      <div class="search-block">
        <form id="searchForm">
          <div class="input-block">
            <div class="wrapper">
              <input id="search" placeholder="Search" type="text">
            </div>
          </div>
          <button id="searchButton" class="btn-block" type="submit">
            <span class="wrapper">
              <i class="fas fa-search"></i>
            </span>
          </button>
        </form>
      </div>
    `;
  }

  setWidthToClips() {
    const bodyWidth = document.body.clientWidth;
    this.clips.forEach((clip) => {
      if (bodyWidth <= 600) {
        clip.style.width = `${this.slider.clientWidth}px`;
      } else if (bodyWidth > 600 && bodyWidth <= 1000) {
        clip.style.width = `${this.slider.clientWidth / 2}px`;
      } else if (bodyWidth > 1000) {
        clip.style.width = `${this.slider.clientWidth / 4}px`;
      }
    });
  }

  static trunkateText(str, length) {
    let res = str;
    if (res.length > length - 4) {
      res = res.substring(0, length - 4);
      res = `${res} ...`;
    }
    return res;
  }

  static formatDate(date) {
    const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    return `${date.getFullYear()}, ${months[date.getMonth()]} ${date.getDate()}`;
  }

  static renderOneClip(clip) {
    return `
      <div class="clip">
        <div class="wrapper">
          <div class="preview">
            <a href="https://www.youtube.com/watch?v=${clip.id}" target="_blank">
              <img src="${clip.preview}" alt="${AppView.trunkateText(clip.title, 100)}}">
            </a>
          </div>
          <div class="info">
            <div class="count-block">
              <span class="icon"><i class="fas fa-eye"></i></span>
              <span class="count">${clip.viewCount}</span>
            </div>
            <div class="info-block">
              <h4 class="title">
                <a href="https://www.youtube.com/watch?v=${clip.id}"  target="_blank">${clip.title}</a>
              </h4>
              <p class="description">${AppView.trunkateText(clip.description, 300)}</p>
            </div>
          </div>
          <div class="footer">
            <div class="date">
              <h5>${AppView.formatDate(clip.publishedAt)}</h5>
            </div>
            <div class="author"><i class="fas fa-user"></i> ${AppView.trunkateText(clip.channelTitle, 23)}</div>
          </div>
        </div>
      </div>
    `;
  }

  renderClips() {
    if (this.clipData) {
      return this.clipData.map(clip => AppView.renderOneClip(clip)).join('');
    }
    return '';
  }

  showNewClips() {
    if (this.clipData) {
      this.sliderContainer.innerHTML = this.renderClips();
      this.clips = this.slider.querySelectorAll('.clip');
      this.setWidthToClips();
    }
  }

  appendClips() {
    if (this.clipData) {
      this.sliderContainer.insertAdjacentHTML('beforeend', this.renderClips());
      this.clips = this.slider.querySelectorAll('.clip');
      this.setWidthToClips();
    }
  }

  static renderSlider() {
    return `
      <div class="slider" id="slider">
        <div class="container" id="sliderContainer"></div>
      </div>
    `;
  }

  renderPagination() {
    return `
      <div class="pagination-block" id="paginationBlock">
        <ul class="pagination">
          <li class="item">
            <button type="button" class="prev">
              <span class="page">...</span>
            </button>
          </li>
          <li class="item">
            <button type="button" class="prevPage">
              <span class="page">${this.currentPage}</span>
            </button>
          </li>
          <li class="item active">
            <button type="button" class="currentPage">
              <span class="page">${this.currentPage + 1}</span>
            </button>
          </li>
          <li class="item">
            <button type="button" class="nextPage">
              <span class="page">${this.currentPage + 2}</span>
            </button>
          </li>
          <li class="item">
            <button type="button" class="next">
              <span class="page">...</span>
            </button>
          </li>
        </ul>
      </div>
    `;
  }

  updatePagination() {
    this.pagination.style.display = this.currentPage === null ? 'none' : 'flex';
    this.pagination.querySelector('.prev').style.display = this.currentPage < 2 ? 'none' : 'block';

    this.pagination.querySelector('.prevPage .page').innerHTML = this.currentPage < 1 ? '' : this.currentPage;
    this.pagination.querySelector('.prevPage').style.display = this.currentPage < 1 ? 'none' : 'block';

    this.pagination.querySelector('.currentPage .page').innerHTML = this.currentPage + 1;
    this.pagination.querySelector('.nextPage .page').innerHTML = this.currentPage + 2;
  }

  render() {
    document.body.innerHTML = `
      ${AppView.renderSearch()}
      ${AppView.renderSlider()}
      ${this.renderPagination()}
    `;
    this.slider = document.getElementById('slider');
    this.sliderContainer = document.getElementById('sliderContainer');
    this.pagination = document.getElementById('paginationBlock');
    this.searchInput = document.getElementById('search');

    this.animateSearchBorder();
    this.updatePagination();
  }
}
