import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor() {
    this.model = new AppModel();
    this.view = new AppView();
    this.slider = null;
    this.sliderContainer = null;
    this.searchForm = null;
    this.searchInput = null;
    this.paginationBlock = null;
  }

  async search() {
    this.model.query = this.searchInput.value;
    const clipData = await this.model.getClipData();
    this.view.clipData = clipData;
    this.view.currentPage = 0;
    this.animate(false);
    this.view.showNewClips();
    this.view.updatePagination();
  }

  async searchNext() {
    if (this.sliderContainer.clientWidth <= this.slider.clientWidth * (this.view.currentPage + 3)) {
      const clipData = await this.model.getClipData();
      this.view.clipData = clipData;
      this.view.appendClips();
    }
  }

  changePagination(e) {
    this.view.setWidthToClips();
    let el = e.target;
    if (el.tagName === 'SPAN') {
      el = e.target.parentElement;
    }
    let index = this.view.currentPage;
    let isRightToleft = true;
    switch (el.className) {
      case 'prev':
        index -= 2;
        isRightToleft = false;
        break;
      case 'prevPage':
        index -= 1;
        isRightToleft = false;
        break;
      case 'nextPage':
        index += 1;
        isRightToleft = true;
        break;
      case 'next':
        index += 2;
        isRightToleft = true;
        break;
      default:
    }
    if (this.sliderContainer.clientWidth > this.slider.clientWidth * index) {
      this.view.currentPage = index;
      this.animate(isRightToleft);
      this.view.updatePagination();
      this.searchNext();
    }
  }

  animate(isRightToleft) {
    const animate = (draw, duration) => {
      const start = performance.now();
      requestAnimationFrame(function animateFn(time) {
        let timePassed = time - start;
        if (timePassed > duration) timePassed = duration;
        draw(timePassed);
        if (timePassed < duration) {
          requestAnimationFrame(animateFn);
        }
      });
    };

    let scrollInterval;
    const scrollLeftEnd = this.view.currentPage * this.slider.clientWidth;
    animate((timePassed) => {
      if (isRightToleft) {
        scrollInterval = scrollLeftEnd - this.slider.scrollLeft;
        this.slider.scrollLeft = this.slider.scrollLeft + timePassed * scrollInterval / 5000;
      } else {
        scrollInterval = this.slider.scrollLeft - scrollLeftEnd;
        this.slider.scrollLeft = this.slider.scrollLeft - timePassed * scrollInterval / 5000;
      }
    }, 1000);
  }

  quickSwipe() {
    let isDown = false;
    let startX = 0;
    let sLeft = 0;
    let walk = 0;

    this.slider.addEventListener('mousedown', (e) => {
      this.view.setWidthToClips();
      isDown = true;
      this.slider.classList.add('active');
      startX = e.pageX - this.slider.offsetLeft;
      sLeft = this.slider.scrollLeft;
    });
    this.slider.addEventListener('mouseleave', () => {
      isDown = false;
      this.slider.classList.remove('active');
    });
    this.slider.addEventListener('mouseup', () => {
      isDown = false;
      this.slider.classList.remove('active');
      this.view.currentPage = Math.floor(this.slider.scrollLeft / this.slider.clientWidth);
      const isRightToleft = walk < 0;
      if (isRightToleft) {
        this.view.currentPage += 1;
      }
      this.animate(isRightToleft);
      this.view.updatePagination();
      this.searchNext();
    });
    this.slider.addEventListener('mousemove', (e) => {
      if (!isDown) return;
      e.preventDefault();
      const x = e.pageX - this.slider.offsetLeft;
      walk = (x - startX) * 1.3;
      this.slider.scrollLeft = sLeft - walk;
    });
  }

  start() {
    this.view.render();
    this.slider = document.getElementById('slider');
    this.sliderContainer = document.getElementById('sliderContainer');
    this.searchForm = document.getElementById('searchForm');
    this.searchInput = document.getElementById('search');
    this.paginationBlock = document.getElementById('paginationBlock');

    this.searchForm.addEventListener('submit', (e) => { this.search(); e.preventDefault(); return false; });

    this.paginationBlock.querySelectorAll('button').forEach((button) => {
      button.addEventListener('click', e => this.changePagination(e));
    });
    this.quickSwipe();
  }
}
