export default class AppModel {
  constructor() {
    this.apiUrl = 'https://www.googleapis.com/youtube/v3';
    this.apiKey = 'AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y';
    this.apiMaxResults = 16;
    this.query = null;
    this.apiPageToken = null;
  }

  static extractClipData(data) {
    return data.items.map(item => ({
      id: item.id,
      title: item.snippet.title,
      description: item.snippet.description,
      channelTitle: item.snippet.channelTitle,
      publishedAt: new Date(item.snippet.publishedAt),
      preview: item.snippet.thumbnails.medium.url,
      viewCount: item.statistics.viewCount,
    }));
  }

  static extractClipIds(data) {
    return data.items.map(item => item.id.videoId);
  }

  getSearchUrl() {
    let url = `${this.apiUrl}/search?key=${this.apiKey}&type=video&part=snippet&maxResults=${this.apiMaxResults}`;
    if (this.query) {
      url = `${url}&q=${this.query}`;
    }
    if (this.apiPageToken) {
      url = `${url}&pageToken=${this.apiPageToken}`;
    }
    return url;
  }

  getVideosUrl(ids) {
    return `${this.apiUrl}/videos?key=${this.apiKey}&type=video&part=snippet,statistics&id=${ids.join(',')}`;
  }

  async getClipData() {
    let responce = await fetch(this.getSearchUrl());
    let data = await responce.json();
    this.apiPageToken = data.nextPageToken;
    const ids = AppModel.extractClipIds(data);
    // console.log(data);

    responce = await fetch(this.getVideosUrl(ids));
    data = await responce.json();
    // console.log(data);

    return AppModel.extractClipData(data);
  }
}
